.cart {
    margin: 2rem auto;
    width: 800px;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    /* padding: 1rem; */
  }
  /* .cart__items {
    flex: 0.7;
    margin-right: 1rem;
  } */
  .cart__summary {
     height: 250px;
    flex: 0.3;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 1rem;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
    border-radius: 10px; 
    margin-top: 20px;
  }
  .summary__title {
    font-size: 1.2rem;
    font-weight: bold;
  }
  .summary__price {
    font-size: 18px;
  }
  .summary_price2 {
    margin-left: 2%;
    font-weight: bold;
  }
  /* .summary__price span:nth-child(1) {
    font-size: 0.9rem;
    flex: 0.6;
  } */
  /* .summary__price span:nth-child(2) {
    flex: 0.4;
    font-size: 1.2rem;
    font-weight: bold;
  } */
  .summary__checkoutBtn {
    padding: 10px 17px;
    border-radius: 10px;
    font-size: 1.2rem;
    font-weight: bold;
    cursor: pointer;
  }
  .summary__checkoutBtn:hover {
    opacity: 0.75;
  }